<template>
  <div class="contentWrap">
    <el-row :gutter="20">
      <el-col :span="11">
        <div class="grid-content bg-purple">
          <dv-border-box-9>
            <div class="box">
              <dv-loading v-if="loadding">Loading...</dv-loading>
              <div v-else>
                <v-title text="碳中和"></v-title>
                <div class="storeContent">
                  <div class="tabs">
                    <div class="active">1F</div>
                    <div>2F</div>
                    <div>3F</div>
                    <div>4F</div>
                  </div>
                  <div class="list">
                    <dv-scroll-board
                      :config="list"
                      style="width: 400px; height: 170px; margin-top: 25px"
                    />
                  </div>
                </div>
              </div>
            </div>
          </dv-border-box-9>
        </div>
      </el-col>
      <el-col :span="13">
        <div class="grid-content bg-purple">
          <dv-border-box-3>
            <div class="box">
              <dv-loading v-if="loadding">Loading...</dv-loading>
              <div v-else>
                <v-title text="碳中和"></v-title>
                <div class="flex">
                  <img src="../assets/lung.png" alt="" />
                  <div
                    id="chart"
                    :style="{
                      width: '300px',
                      height: '205px',
                      marginTop: '-25px',
                    }"
                  ></div>
                </div>
              </div>
            </div>
          </dv-border-box-3>
        </div>
      </el-col>
    </el-row>
    <el-row :gutter="20">
      <el-col :span="10">
        <div class="grid-content bg-purple">
          <dv-border-box-8>
            <div class="box">
              <dv-loading v-if="loadding">Loading...</dv-loading>
              <div v-else>
                <v-title text="信息同比"></v-title>
                <div
                  id="contrastChart"
                  :style="{
                    width: '470px',
                    height: '285px',
                    marginTop: '-25px',
                  }"
                ></div>
              </div>
            </div>
          </dv-border-box-8>
        </div>
      </el-col>
      <el-col :span="14">
        <div class="grid-content bg-purple">
          <dv-border-box-9>
            <div class="box">
              <dv-loading v-if="loadding">Loading...</dv-loading>
              <div v-else>
                <v-title text="量统计"></v-title>
                <div
                  id="statisticsChart"
                  :style="{
                    width: '660px',
                    height: '245px',
                    marginTop: '-25px',
                  }"
                ></div>
              </div>
            </div>
          </dv-border-box-9>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import title from "../components/title.vue";
export default {
  data: () => {
    return {
      loadding: false,
      list: {
        data: [
          //商铺电费列表
          ["101 恒昌药店", "用水量15564t", "碳中和"],
          ["101 恒昌药店", "用水量15564t", "碳中和"],
          ["101 恒昌药店", "用水量15564t", "碳中和"],
          ["101 恒昌药店", "用水量15564t", "碳中和"],
          ["101 恒昌药店", "用水量15564t", "碳中和"],
          ["101 恒昌药店", "用水量15564t", "碳中和"],
          ["101 恒昌药店", "用水量15564t", "碳中和"],
        ],
        rowNum: 5,
        oddRowBGC: "#122A56",
        evenRowBGC: "#1D4282",
      },
    };
  },
  components: {
    "v-title": title,
  },
  mounted: function () {
    // 绘制echarts
    this.drawLine();
  },
  methods: {
    drawLine: function () {
      //对比柱状图
      let contrastChart = this.$echarts.init(
        document.getElementById("contrastChart")
      );
      contrastChart.setOption({
        tooltip: {
          trigger: "axis",
        },
        legend: {
          data: ["废气", "氧气"],
          textStyle: {
            color: "#fff",
          },
        },
        xAxis: {
          type: "category",
          data: ["1F", "2F", "3F", "4F"],
          splitLine: {
            show: false,
          },
          axisLabel: {
            color: "#fff",
          },
        },
        yAxis: {
          type: "value",
          splitLine: {
            show: false,
          },
          axisLabel: {
            color: "#fff",
          },
        },
        series: [
          {
            name: "废气",
            data: [120, 200, 150, 80],
            type: "bar",
            itemStyle: {
              color: "#00FFFF",
            },
          },
          {
            name: "氧气",
            data: [120, 200, 150, 80],
            type: "bar",
            itemStyle: {
              color: "#1289F8",
            },
          },
        ],
      });
      //统计折线图 statisticsChart
      let statisticsChart = this.$echarts.init(
        document.getElementById("statisticsChart")
      );
      statisticsChart.setOption({
        tooltip: {
          trigger: "axis",
        },
        legend: {
          data: ["公区", "商铺", "流量"],
          textStyle: {
            color: "#fff",
          },
        },
        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true,
        },
        xAxis: {
          type: "category",
          boundaryGap: false,
          axisLabel: {
            color: "#fff",
          },
          splitLine: {
            show: true,
            lineStyle: {
              color: "rgba(98, 217, 255, .3)",
            },
          },
          data: [
            "1月",
            "2月",
            "3月",
            "4月",
            "5月",
            "6月",
            "7月",
            "8月",
            "9月",
            "10月",
            "11月",
            "12月",
          ],
        },
        yAxis: {
          type: "value",
          axisLabel: {
            color: "#fff",
          },
          splitLine: {
            show: true,
            lineStyle: {
              color: "rgba(98, 217, 255, .3)",
            },
          },
        },
        series: [
          {
            name: "公区",
            type: "line",
            data: [
              120, 132, 101, 134, 90, 230, 210, 220, 182, 191, 234, 290, 330,
            ],
            smooth: true,
            symbol: "none",
            itemStyle: {
              color: "#FFB421",
            },
          },
          {
            name: "商铺",
            type: "line",
            data: [
              220, 182, 191, 234, 290, 330, 310, 150, 232, 201, 154, 190, 330,
            ],
            smooth: true,
            symbol: "none",
            itemStyle: {
              color: "#8FC31F",
            },
          },
          {
            name: "流量",
            type: "line",
            data: [150, 232, 201, 154, 190, 330, 410, 120, 132, 101, 134, 90],
            smooth: true,
            symbol: "none",
            itemStyle: {
              color: "#62D9FF",
            },
          },
        ],
      });
      //未知面积图
      let chart = this.$echarts.init(document.getElementById("chart"));
      chart.setOption({
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "cross",
            label: {
              backgroundColor: "#6a7985",
            },
          },
        },
        grid: {
          left: "0%",
          bottom: "10%",
          containLabel: true,
        },
        xAxis: [
          {
            type: "category",
            boundaryGap: false,
            data: [
              "1月",
              "2月",
              "3月",
              "4月",
              "5月",
              "6月",
              "7月",
              "8月",
              "9月",
              "10月",
              "11月",
              "12月",
            ],
            axisLabel: {
              color: "#fff",
            },
          },
        ],
        yAxis: [
          {
            type: "value",
            axisLabel: {
              color: "#fff",
            },
            splitLine: {
              show: true,
              lineStyle: {
                color: "#34496F",
                type: "dashed",
              },
            },
          },
        ],
        series: [
          {
            name: "物业费",
            type: "line",
            stack: "总量",
            areaStyle: {
              color: "#52b3d9",
            },
            emphasis: {
              focus: "series",
            },
            data: [120, 132, 101, 134, 90, 230, 210, 32, 321, 544, 657, 87],
            smooth: true,
          },
        ],
      });
    },
  },
};
</script>

<style lang="less" scoped>
.el-row {
