  &:last-child {
    margin-bottom: 0;
  }
}
.grid-content {
  height: 380px;
}
.row-bg {
  padding: 10px 0;
}

.contentWrap {
  .storeContent {
    display: flex;
    align-items: center;
    justify-content: space-between;
    .tabs {
      width: 56px;
      height: 180px;
      margin-top: 25px;
      overflow: scroll;
      overflow-x: hidden;
      &::-webkit-scrollbar {
        width: 0 !important;
      }
      div {
        height: 32px;
        width: 100%;
        color: #fff;
        border-radius: 8px;
        line-height: 32px;
        text-align: center;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        margin-bottom: 20px;
        box-sizing: border-box;
        padding: 0 10px;
      }
      .active {
        background: #3a7ff2;
      }
    }
  }
  .flex {
    display: flex;
    align-items: center;
    margin-top: 20px;
    justify-content: space-between;
    img {
      width: 363px;
      height: 221px;
      object-fit: cover;
    }
  }
}
</style>